<template>
  <div>

    <el-row v-for="(item, i) in api_list" :key="i" style="background-color:whitesmoke; margin-top: 0.5%; "
      :id="(item.root + item.path).replace(/\//g, '_')">
      <el-col :span="12">
        <div class="grid-content bg-purple">
          <h1 style="color: darkgreen;"># {{ item.name.zh }}</h1>
          <h2>{{ item.api ? " "+item.api.method +  " " + item.api.uri : '' }}</h2>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
          <div>{{ item.root + item.path }}</div>
        </div>
      </el-col>
      <el-col :span="12">
        <el-tabs type="border-card">
          <el-tab-pane label="header">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>
          <el-tab-pane label="payload">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>
          <el-tab-pane label="response">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>
          <el-tab-pane label="java">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>
          <el-tab-pane label="golang">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>
          <el-tab-pane label="C#">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>
          <el-tab-pane label="nodeJS">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>
          <el-tab-pane label="python">
            <div><el-input style="color: green;" type="textarea" :autosize="{ minRows: 25, maxRows: 90 }" placeholder="" v-model="textarea2">
              </el-input></div>
          </el-tab-pane>

        </el-tabs>
      </el-col>
    </el-row>


    <div id="_argjaogrhaorghaogh-rea" v-scroll-to="'#logo'">
      Hi I'm #element
    </div>

  </div>
</template>

<script>
export default {
  name: 'app',
  created() {
    this.api_list = []
    const modulepath = this.$route.path.split('/')[1]
    console.log(modulepath)
    const files = require.context("@/data", true, /\.json$/).keys();
    for (let i = 0; i < files.length; i++) {
      const jsonfile = files[i]
      if (jsonfile.indexOf("/" + modulepath + "/")) {
        const oneMenu = require("@/data/" + jsonfile.replace("./", ""))
        console.log(oneMenu)

        if (oneMenu.children && oneMenu.children.length > 0) {
          for (let j = 0; j < oneMenu.children.length; j++) {
            const data = {
              root: oneMenu.root,
              path: oneMenu.path,
              name: oneMenu.name,
              api: '',
              comment: '',
            }
            const child = oneMenu.children[j]
            data.path += "/" + child.path
            if (child.comment)
              data.comment = child.comment
            if (child.api)
              data.api = child.api
            if (child.name)
              data.name = child.name
            this.api_list.push(data)
          }

        } else {
          const data = {
            root: oneMenu.root,
            path: oneMenu.path,
            name: oneMenu.name,
            api: '',
            comment: '',
          }
          if (oneMenu.comment)
            data.comment = oneMenu.comment
          if (oneMenu.api)
            data.api = oneMenu.api
          this.api_list.push(data)
        }


      }

    }
  },
  watch: {
    '$route.path': function (to, from) {
      if (to != from)
        this.$scrollTo('#' + this.$route.path.replace(/\//g, '_'), 10, { easing: 'ease', cancelable: false })
      if (to.split('/')[1] != from.split('/')[1]) {
        alert(to.split('/')[1])
        // this.$scrollTo('#'+this.$route.path.replace(/\//g,'_'), 1000, { easing: 'ease', cancelable: false })

      }
    }
  },
  mounted() {
    console.log(this.api_list)
    this.$scrollTo('#' + this.$route.path.replace(/\//g, '_'), 10, { easing: 'ease', cancelable: false })
  },
  data() {
    return {
      api_list: [],
      textarea2: `package main

import "fmt"

func main() {
   const LENGTH int = 10
   const WIDTH int = 5  
   var area int
   const a, b, c = 1, false, "str" //多重赋值

   area = LENGTH * WIDTH
   fmt.Printf("面积为 : %d", area)
   println()
   println(a, b, c)  
}`
    }
  }
}
</script>


<style>
.el-textarea__inner {
  background-color: black;
  color: greenyellow;
}
</style>


